button {
  border: none;
}

input {
  border: none;
}

.main {
  transition: filter .5s ease;
}

.scroll-disabled {
  overflow: hidden;
  .main {
    overflow: hidden;
    filter: blur(15px);
    transition: filter .5s ease;
  }
  .body-overlay {
    display: block;
  }
}

.container {
  max-width: 1280px;    
  margin: 0 auto;
}

#wrapper {
  position: relative;
}

.swiper {
  width: 100%;
  height: 525px;

  @include desktop {
    max-width: 1023px;
    width: auto;
    height: auto;
    margin: 0;
  }
}

.slide-content {
  position: absolute;
  bottom: 0;
  margin-bottom: 96px;
  margin-left: 17px;

  @include desktop {
    margin-left: 196px;
    margin-bottom: 191px;
  }

  h2 {
    position: relative;
    color: #fff;
    margin-bottom: 30px;
    @include fontSize(24px, 500);

    @include desktop {
      margin-bottom: 20px;
      @include fontSize(46px, 500);
    }
  }

  .slide-btn {
    position: relative;
    @include fontSize(16px, 500);
    padding: 9px 24px 9px 20px;
    background-color: #fff;

    @include desktop {
      padding: 14px 50px 14px 45px;
      @include fontSize(18px, 500);
    }
  }
}

.slide-image {
  height: 100%;

  @include desktop {
    height: auto;
  }
}

.slide-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;

}

.swiper-button-prev,
.swiper-button-next {
  outline: none;
  position: absolute;
  &:hover {
    &::after {
      content: url("data:image/svg+xml,%3Csvg width='46' height='45' viewBox='0 0 46 45' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='23.386' cy='22.5' r='22.5' fill='%23606C38'/%3E%3Cpath d='M21 16L27 22L21 28' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      transition: content .7s ease;
    }
  }

  &::after {
    content: url("data:image/svg+xml,%3Csvg width='38' height='38' viewBox='0 0 38 38' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='19' cy='19' r='19' fill='white'/%3E%3Cpath d='M18 13L24 19L18 25' stroke='%23606C38' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    display: inline-block;
    height: 45px;
    transition: content .7s ease;
  }

  @include desktop {
    &::after {
      content: url("data:image/svg+xml,%3Csvg width='46' height='45' viewBox='0 0 46 45' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='23.386' cy='22.5' r='22.5' fill='white'/%3E%3Cpath d='M21 16L27 22L21 28' stroke='%23606C38' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    }

  }


}

.swiper-button-prev {
  &:after {
    transform: scale(-1, 1);
    margin-left: 55px;
  }
}

.swiper-button-next {
  &::after {
    margin-right: 55px;
  }
}

.banner {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 15px;
  margin-bottom: 51px;


  @include desktop {
    top: auto;
    position: relative;
    margin-bottom: 85px;
    flex-direction: row;
    justify-content: space-between;
  }
}

.aside-banner {
  display: flex;
  gap: 15px;
  touch-action: auto;
  overflow-x: scroll;

  @include desktop {
    touch-action: pan-x;
    overflow-x: auto;
    gap: unset;
    flex-direction: column;
    justify-content: space-between;
  }

  .banner-item {
    img {
      max-width: 236px;
    }
  }
}

.swiper-pagination-bullet {
  display: inline-block;
  height: 8px;
  width: 8px;
  background-color: #fff;
  opacity: 1;

  @include desktop {
    height: 10px;
    width: 10px;
  }
}

.swiper-pagination-bullet-active {
  @extend .swiper-pagination-bullet;
  background-color: #606C38;
}

#swiper-pagination {
  text-align: left;
  left: 17px;
  bottom: 61px;

  @include desktop {
    text-align: right;
    left: auto;
    right: 55px;
  }
}

.products {
  margin-bottom: 85px;

  .container {
    display: flex;
    align-items: stretch;
    flex-direction: column-reverse;

    @include desktop {
      flex-direction: row;
    }

    .category-content {
      display: none;

      @include desktop {
        display: flex;
      }
    }

    }
    .category-menu {
      display: flex;
    }

    .category-holder {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
    }

    .category-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      align-self: stretch;
      height: auto;
      padding: 23px 33px;
      background-color: #fff;
      border: 1px solid rgba(0, 0, 0, 0.1);

      @include desktop {
        padding: 35px 53px;
      }


      img {
        object-fit: contain;
        width: 120px;
        min-width: 120px;
        margin-bottom: 10px;
        max-height: 120px;
        max-width: unset;

        @include desktop {
          width: 160px;
          margin-bottom: 10px;          
        }
      }
    }

    .pdouct-menu {
      display: none;
      @include desktop {
        display: flex;
        align-items: flex-start;
        width: 264px;
        padding: 40px 18px 40px 40px;
        align-self: stretch;  
        flex-grow: 1;
  
      }

      ul {
        max-width: 206px;
      }

      li {
        margin-bottom: 14px;
        @include fontSize($font-body-large, 300);
        text-decoration: underline;
        color: #606C38;
      }


  }

  .card-title {
    padding: 8px 14px;
    background: map-get($brand-color-theme, secondary-color);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include desktop {
      padding: 25px 27px;
      padding-right: 76px;
    }


    h2 {
      @include fontSize(20px, 500);

      @include desktop {
        @include fontSize(32px, 500);
      }
    }
  }




  .buttons {
    display: flex;
    gap: 10px;

    &>:nth-child(n) {
      width: 38px;
      height: 38px;

      @include desktop {
        width: 45px;
        height: 45px;
      }

    }

    .row-prev-btn {
      transform: scale(-1, 1);
    }


    .disabled {
      path {
        stroke: rgba(96, 108, 56, 0.3);
      }
    }
    .swiper-button-disabled {
      path {
        stroke: map-get($slider-btn-color-theme, disabled-inner);
      }
      circle {
        fill: map-get($slider-btn-color-theme, disabled-outer);
      }

      &:hover {
        circle {
          fill: map-get($slider-btn-color-theme, disabled-outer);
          transition: fill .7s ease;
  
        }
        path {
          stroke: map-get($slider-btn-color-theme, disabled-inner);
          transition: stroke .7s ease;
        }
      }

    }
  }
  .row-prev-btn, .row-next-btn {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all .5s ease;

    &:hover {
      circle {
        fill: map-get($slider-btn-color-theme, hovered-outer);
        transition: fill .7s ease;

      }
      path {
        stroke: map-get($slider-btn-color-theme, hovered-inner);
        transition: stroke .7s ease;
      }
    }

  }








  .product-row {
    flex-grow: 1;
  }

  .card-holder {
    position: relative;
    flex-grow: 1;
    overflow: hidden;
    flex-wrap: nowrap;
    box-sizing: border-box;
    //


    @include desktop {
      overflow: unset;
      flex-wrap: wrap;
    }

    &>:nth-child(n + 3) {
      display: none;

      @include desktop {
        display: flex;
      }
    }
  }

  .card {
    width: 50%;
    height: auto;
    border: 1px solid rgba(0, 0, 0, 0.1);

    @include desktop {
      width: 33.333%;
    }
  }

  .card-row {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  div[class*="popular"] {
    margin-bottom: 85px;
  }

  .liked-icon {
    width: 22px;
    height: 20px;
    stroke-width: 2px;
    stroke: black;
    fill: transparent;
    transition:  fill .5s ease;


    &:hover {
      stroke: $btn-liked-hovered;
      fill: $btn-liked-hovered;
    }
  }
}

.popular {
  .aside-image {
    overflow: hidden;
    height: 250px;
    
    @include desktop {
      display: block;
      width: 40%;
      height: auto;
    }

    img {
      object-fit: cover;
    }
  }


  .product-row {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;

    @include desktop {
      margin-bottom: 0;
      width: 60%;
    }
  }
  .card {
    border-left: unset;
  }

  .card-holder >:nth-child(n + 4){
    border-top: unset;
  }


}


.popular-2 {
  overflow: hidden;
  .--transparent-background {
    background: transparent;
  }
  .product-row{
    width: 100%;
  }

  .card-title {
    padding-left: 0;
    padding-right: 0;
  }

  .container {
    flex-direction: column;
  }

  .card {
    width: 50%;

    @include desktop {
      width: 20%;
    }
  }
  .card + .card {
    border-left: unset;
  }
  
}

.popular-3 {
  .product-row {
    display: flex;
    flex-direction: column;
    @include desktop {
      width: 60%;
    }
  }
  .card {
    border-left: unset;
  }

  .pdouct-menu {
    border-left: unset;
  }

  .category-holder {
    .category-item {
      height: 50%;
    }
  }
  .category-holder >:first-child  {
    border-bottom: unset;
  }

}

.popular-4 {
  .container {
    flex-direction: column-reverse;
    
    
    @include desktop {
      flex-direction: row-reverse;

    }
  }
  

  .product-row {
    width: 100%;
    
    @include desktop {
      width: 60%;

    }
  }

  .category-col {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row-reverse;

    @include desktop {
      width: 40%;      
    }
    & >:nth-child(n) {
      width: 50%;
    }
    & >:first-child {
      width: 100%;
    }
  }

  .card-row {
    height: auto;
  }
  .card {
    border-left: unset;
  }

  .category-col >:nth-child(2n) {
    border-left: unset;
  }

  .category-col >:nth-child(n + 2) {
    border-top: unset;
  }
  
}

.popular-5 {
  margin-bottom: 105px;

  .category-col {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    @include desktop {
      width: 40%;
    }

  }
  .card-row {
    height: 100%;
  }


  .product-row {
    width: 100%;
    display: flex;
    flex-direction: column;

    @include desktop {
      width: 60%;
    }
  }

  .category-item {
    width: 50%;

    @include desktop {
      width: 256px;
      height: 257px;
    }
  }
   
   
  .card {
    border-left: unset;
  }
  .card-holder >:nth-child(n + 4){
    border-top: unset;
  }

  .category-col >:nth-child(2n) {
    border-left: unset;
  }

  .category-col >:nth-child(n + 3) {
    border-top: unset;
  }

}


.body-overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 5;
}

