.card-holder {
    display: flex;
    flex-wrap: wrap;

    .card {
        display: flex;
        flex-direction: column;
        padding: 35px 24px;
        width: 25%;
        background-color: #fff;
    }


    .card-activity {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    .free-shipping {
        background-color: #DEEBC1;
        @include fontSize(12px, 300);
        border-radius: 2px;
        padding: 3px 5px;
    }


    .card-img {
        margin-bottom: 15px;
        align-self: center;
    }
    .card-body {
        display: flex;
        flex-direction: column  ;
        h2 {
            @include fontSize($font-body-large, 300);
            max-width: 195px;
            margin-bottom: 10px;
        }
    }

    .rate {
        display: flex;
        gap: 3px;
        margin-bottom: 8px;

        .star {
            fill: #E5E5E5;
        }

        .active {
            fill: #606C38;
        }
    }

    .price {
        margin-bottom: 14px;
    }
    .buy-btn {
        flex-shrink: 1;
        background-color: #606C38;
        padding: 10px 0 10px 0;
        text-align: center;
        color: #fff;
        @include colorTransition($btn-card-hovered);
    }
}

.products-row {
    flex-grow: 1;
}
