.sticky-bottom {
    padding: 10px 26px 5px 26px;
    background: #DEEBC1;
    position: fixed;
    bottom: 0;
    z-index: 20;
    width: 100%;
    left: 0;

    @include desktop {
        display: none;
    }
    .interativity-holder {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }

    .bottom-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    a > svg {
        margin-bottom: 8px;
    }
    span {
        @include fontSize(12px, 300);
    }

}