.registration-popup {
    position: fixed;
    @include fontSize(14px, 300);
    top: 0px;
    right: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 50;
    height: 100vh;
    overflow-y: scroll;
    transform: translateX(110%);
    transition: transform .7s ease;

    // scroll bar disabling 
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar {
        display: none;
    }
    @include desktop {
        width: 360px;
        margin-top: 67px;
        height: calc(100vh - 67px);
    }

    .registration-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 13px 16px 16px 42px;
        background: #fff;
        position: sticky;
        top: 0;
        z-index: inherit;
        
        
        span {
            @include fontSize(20px, 500);
        }
        
        @include desktop {
            span {
                @include fontSize(20px, 500);
            }
            
        }

    }


    .close-icon {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 36px;
        height: 36px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;

        &:after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(45deg);
        }

        span {
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(-45deg);
        }

    }

    .registration-holder {
        padding: 26px 16px;
        background-color: map-get($brand-color-theme, bg-gray);
    }

    .form-registration {
        display: flex;
        flex-direction: column;
        label {
            margin-bottom: 6px;
            @include fontSize(12px, 300);
        }
        input {
            width: 100%;
            padding: 11px 5px 18px 15px;
            @include fontSize($font-body-large, 300);
        }
        input:nth-of-type(n) {
            margin-bottom: 17px;
        }
        
    }
    .password-holder {
        flex-grow: 1;
        display: flex;
        align-items: center;
        margin-bottom: 17px;
        position: relative;

        .password-icon {
            position: absolute;
            right: 14px;
        }

        #registration-password {
            margin-bottom: 0;
        }

    }

    .license-agreement {
        @include fontSize(12px, 300);
        margin-bottom: 32px;
        p {
            font-size: inherit;
        }

        & >:first-child {
            margin-bottom: 17px;
        }
    }

    .registration-helpers {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 32px;
    }
    .chekbox-holder {
        font-weight: 500;
        display: flex;
        align-items: stretch;

        label {
            margin-left: 7px;
            font-weight: 300;
        }
        #remember-me-checkbox {
            appearance: none;
            width: 16px;
            height: 16px;
            border: 1px solid rgba(0, 0, 0, 0.1);
            margin: 0;
            background-color: map-get($slider-btn-color-theme, basic-inner);
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;

            &:checked {
                &::after {
                    content: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4L3.1 7L8 1' stroke='white' stroke-width='2'/%3E%3C/svg%3E%0A");
                    display: inline-block;
                    position: absolute;
                }
            }
        }
    }
    .forget-pswd {
        color: map-get($slider-btn-color-theme, basic-inner);
        text-decoration: underline;
    }

    .registration-buttons {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        margin-bottom: 33px;

        button {
            padding: 16px 5px;
            cursor: pointer;
        }
    }

    .registration-btn {
        background-color: map-get($slider-btn-color-theme, basic-inner);
        color: map-get($slider-btn-color-theme, basic-outer);
    }

    .registred-btn {
        color: map-get($slider-btn-color-theme, basic-inner);
    }

    .registration-socials {
        display: flex;
        flex-direction: column;
        
        .title {
            margin-bottom: 14px;
        }

        a {
            padding: 14px 5px;
            text-align: center;
            border-radius: 60px;
            border: 1px solid rgba(0, 0, 0, 0.1);
            background-color: map-get($slider-btn-color-theme, basic-outer);
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            align-items: center;
        }
        svg {
            margin-right: 9px;
            height: 18px;
        }
        
    }

    .facebook-btn {
        margin-bottom: 7px;
    }
    .login-google {
        width: 18px;
    }
}