$search-interativity-category: url("data:image/svg+xml,%3Csvg width='10' height='15' viewBox='0 0 10 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L8 7.5L1 14' stroke='%23606C38' stroke-width='2'/%3E%3C/svg%3E%0A");

$search-interactivity-main-bg: #EFEFEF;
$search-interactivity-secondary-bg: #fff;
$primary-color: #606C38;



.search-interactivity {
    position: absolute;
    top: 100%;
    z-index: 20;
    background-color: #EFEFEF;
    clip: rect(auto, auto, 0, auto);
    // scroll bar disabling 
    

    .search-holder {
        height: 100vh;
        overflow-y: scroll;
        overflow-x: hidden;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
        &::-webkit-scrollbar {
            display: none;
        }
    
        
    }



    .search-suggestions {
        background-color: $search-interactivity-secondary-bg;
        li {
            padding: 18px 14px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            display: flex;
            align-items: center;
            &::before {
                content: $search-interativity-suggestions;
                display: block;
            }
            a {
                margin-left: 10px;
            }
        }
    }

    .search-category {
        li {
            margin: 21px 15px 16px 21px;
            position: relative;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            display: flex;
            justify-content: space-between;
            align-items: center;

            &::after {
                content: $search-interativity-category;
                display: block;
                position: absolute;
                right: 0

            }
            
        }
        a {
            @include fontSize(14px, 500);
            display: flex;
            flex-direction: column;
            width: 100%;
        }
        
        span {
            color: $primary-color;
            @include fontSize(12px, 300);
            margin-bottom: 16px;
            padding-top: 5px;
        }
    }

    .search-result {
        padding: 28px 14px 28px 28px;

        li {
            margin-bottom: 25px;
        }

        a {
            display: flex;
            align-items: center;

            img {
                width: 60px;
                height: 60px;
                object-fit: scale-down;
            }
        }

        .item-descr {
            display: flex;
            flex-direction: column;
        }
        .item-title {
            margin-bottom: 7px;
        }

        .price-container {
            span + span {
                margin-left: 11px;
            }
        }
    }
}

.header .search-active {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    padding: 10px 25px;
    z-index: 40;
    background-color: map-get($brand-color-theme, primary-color);
    transition: all .7s ease;

    @include desktop {
        position: relative;
        background-color: unset;
        padding: 0;
        width: 38.5vw;

    }
    .search-interactivity {
        top: 52px;
        width: 100%;
        left: 0;
        clip: rect(auto, auto, 3000px, auto);
        transition: clip .7s ease;
    }

    .close-btn {
        visibility: visible;
        opacity: 1;
        transition: .5s ease;

    }

    .voice-btn {
        visibility: hidden;
        opacity: 0;
    }

}
