.brand-slider {
    margin-bottom: 85px;
  
    .container {
      flex-direction: row;
    }
  
    .brand-title {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      h2 {
        @include fontSize(32px, 500);
        margin-bottom: 6px;
      }
    }
    .brand-holder {
      overflow: hidden;
      margin-left: 50px;
    }
    .brand-item {
      width: 165px;
  
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: map-get($brand-color-theme, bg-white);
      @include colorTransition($btn-brand-hovered);
  
    }
    .category-thumb {
      margin: 5px 0;
    }
    .brand-item + .brand-item {
      border-left: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
  