.testing-pages {
    position: fixed;
    display: flex;
    flex-direction: column-reverse;
    left: 20px;
    bottom: 20px;
    width: 200px;
    z-index: 50;
    background-color: rgba($color: #000000, $alpha: 0.2);

    &:hover .testing-holder {
        max-height: 1000px;
    }

    span {
        display: block;
        padding: 30px 0;
        text-align: center;
        border: 2px solid rgba($color: #fff, $alpha: 0.5);
        
    }


    a {
        display: block;
        padding: 30px 0;
        text-align: center;
        border: 2px solid rgba($color: #fff, $alpha: 0.5);
    }
    .testing-holder {
        max-height: 0;
        overflow: hidden;
        transition: all .7s ease;
    }
}