.liked-popup {
    position: fixed;
    top: 0px;
    right: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 50;
    height: 100vh;
    overflow-y: scroll;
    transform: translateX(110%);
    transition: transform .7s ease;

    // scroll bar disabling 
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar {
        display: none;
    }

    @include desktop {
        width: 360px;
        margin-top: 67px;
        height: calc(100vh - 67px);
    }



    .card-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 13px 16px 16px;
        background: #fff;
        position: sticky;
        top: 0;
        z-index: inherit;
        
        
        span {
            @include fontSize(20px, 500);
        }
        
        @include desktop {
            padding: 13px 16px 16px;
            span {
                @include fontSize(20px, 500);
            }
            
        }

    }


    .close-icon {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 36px;
        height: 36px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;

        &:after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(45deg);
        }

        span {
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(-45deg);
        }

    }
    .cart-count {
        @include fontSize(16px,300);
    }


    .cart-holder {
        display: flex;
        flex-direction: column;
        background: #fff;
        position: relative;
        overflow: scroll;

        @include desktop {
            overflow: auto;
        }
    }

    .cart-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 22px 20px 24px 20px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        position: relative; 
        //
        flex-wrap: wrap;
        
        span {
            @include fontSize(14px, 300);
            
        }
        @include desktop {
            
            span{
                @include fontSize(16px, 300);
            }
        }
    }
    

    .item-holder {
        display: flex;
        align-items: center;
    }

    .item-col {
        display: flex;
        justify-content: center;
        align-items: center;
        //
        margin-bottom: 20px;

        @include desktop {
            margin-bottom: 0;
        }

        img {
            object-fit: scale-down;
            width: 75px;
            height: 75px;
            min-width: 75px;
            min-height: 75px;
            display: block;
        }
    }

    .item-descr {
        display: flex;
        margin-left: 21px;
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: wrap;


    }

    .item-title {
        margin-bottom: 15px;
        max-width: 166px;
    }



    .item-counter {
        display: flex;
        align-items: center;

        .minus-btn {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 10px;
            height: 10px;

            span {
                display: block;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
            }
        }

        .plus-btn {
            margin-left: 12px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            width: 10px;
            height: 10px;

            &::after {
                content: '';
                display: block;
                position: absolute;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
                transform: rotate(90deg);
            }

            span {
                display: block;
                position: absolute;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
            }
        }

        .item-count {
            margin-left: 12px;
            width: 50px;
            @include fontSize(14px, 300);
            border: 1px solid rgba(0, 0, 0, 0.1);
            color: #000;
            text-align: center;
            padding: 5px 0;

        }
    }
    .cart-price {
        align-self: center;
        span {
            @include fontSize(18px,500);
        }
    }

    .price-discount {
        display: none;
    }

    .cart-total {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        background-color: #EFF0EB;
        border: 1px solid rgba(96, 108, 56, 0.3);
        position: sticky;
        width: 100%;
        bottom: 23px;
        padding: 25px 20px 21px 20px;

        .submit {
            @include fontSize(16px, 500);
            padding: 15px 10px;
            background-color: #606C38;
            border: none;
            color: #fff;
            align-self: stretch;
            cursor: pointer;
            width: 100%;
            
            @include desktop {
                padding: 14px 91px;
                align-self: flex-end;

            }
        }
    }

    .sum-container {
        display: flex;
        align-items: center;
        margin-bottom: 11px;
        justify-content: space-between;
        width: 100%;

        @include  desktop {
            margin-bottom: 14px;
            span {
                @include fontSize(18px, 500);
            }
        }

        span {
            @include fontSize(18px, 500);
        }

        .total-sum {
            margin-left: 22px;
            @include fontSize(18px, 500);

            }
    }
    .cart-etc {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        //
        position: absolute;
        top: 19%;
        right: 19px;
        cursor: pointer;


        span,
        &::after,
        &::before {
            width: 3px;
            height: 3px;
            border-radius: 100%;
            background-color: #000;
            overflow: hidden;
        }

        &::before,
        &::after {
            content: '';
            display: block;
        }

        span,
        &::before {
            margin-bottom: 3px;
        }
    }

    .cart-container {
        padding: 18px 16px 23px;
        background-color: #EFEFEF;
    }

    

}