@use "sass:map";
@import '../components/var-list';

* {
  font-size: 14px;
}

body {
  background-color: map-get($brand-color-theme, body-color);
  font-family: 'Oswald' , Arial, Helvetica, sans-serif;


  &::-webkit-scrollbar {
    display: none;
  }

  -ms-overflow-style: none;
  /* for Internet Explorer, Edge */
  scrollbar-width: none;
  /* for Firefox */
  -webkit-tap-highlight-color: transparent;


}

input,
a {
  &:focus {
    outline: none;
  }
}

li {
  list-style: none;

}

a {
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}


.header {
  position: fixed;
  top: 0;
  transition: background-color .5s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 13px;
  background: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  padding: 0 14px;
  width: 100%;
  margin-bottom: 22px;
  z-index: 5;

  @include desktop {
    background-color: map-get($brand-color-theme, primary-color);
    position: sticky;
    border-bottom: none;
    gap: 0;
    padding: 0;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 47px;

  }

  .burger {
    position: relative;
    align-self: stretch;
    padding: 20px 0;
    cursor: pointer;

    @include desktop {
      display: none;
    }


    span {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 4px;

      &:before {
        content: '';
        display: inline-block;
        width: 13px;
        border-bottom: 2px solid #FFFFFF;
        border-radius: 2px;
      }

      &:after {
        content: '';
        display: inline-block;
        width: 6px;
        border-bottom: 2px solid #DEEBC1;
        border-radius: 2px;
        margin-left: 3px;
      }
    }

    &::after,
    &::before {
      content: '';
      display: block;
      width: 100%;
      border-bottom: 2px solid #FFFFFF;
      border-radius: 2px;
    }

    &::before {
      margin-bottom: 4px;
    }
  }

  .header-interactivity {
    flex-grow: 1;
    display: flex;

    &>:last-child {
      display: none;

      @include desktop {
        display: flex;
      }
    }

    @include desktop {
      flex-direction: column;
    }
  }

  .header-interactivity-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;

    @include desktop {
      justify-content: center;
    }
  }

  .frequent-goods {
    display: none;

    @include desktop {
      display: flex;
      padding: 9px 0 20px;
      color: #fff;
      transition: all .5s ease;
      @include fontSize(12px, 300);

      span {
        font-size: inherit;
      }

      a+a {
        padding-left: 10px;
      }

      span+a {
        padding-left: 13px;
      }

      a {
        text-decoration: none;
        color: inherit;

        &:hover {
          transition: all .5s ease;
          color: rgba(255, 255, 255, 0.6);
        }
      }
    }
  }

  .search-container {
    display: flex;
    align-items: center;
    position: relative;
    width: auto;
    transition: all .7s ease;



    @include phone {
      width: 38.5vw;
    }

    @include desktop {
      align-self: stretch;
      margin-left: 11px;
    }


    .search-input {
      display: inline-block;
      padding: 4px 0 9px 9px;
      width: 100%;
      @include fontSize($font-body-small, 300);

      @include desktop {
        height: 100%;
        padding: 7px 0 12px 39px;
        @include fontSize($font-body-large, 300);
      }

      &::placeholder {
        color: $input-color;
      }
    }

    .voice-btn {
      position: absolute;
      display: inline-block;
      padding: 6px 12px;
      right: 0;
      border-left: 1px solid rgba(0, 0, 0, 0.1);
      max-width: 40px;
      max-height: 40px;
      cursor: pointer;
      transition: all .5s ease;
    }

    .close-btn {
      position: absolute;
      visibility: hidden;
      opacity: 0;
      right: 15px;
      margin-right: 15px;
      transition: .5s ease;
      padding: 6px 0px;
      padding-left: 12px;

      @include desktop {
        right: 12px;
        border-left: 1px solid rgba(0, 0, 0, 0.1);
        margin-right: 0;
      }

    }



  }

  .search-icon {
    display: none;
    position: absolute;
    left: 15px;
    max-width: 16px;
    max-height: 16px;
    stroke: white;

    @include desktop {
      display: inline-block;
      position: static;

    }
  }


  .search-btn {
    display: none;
    

    @include desktop {
      display: inline-block;
      padding: 14px 15px;
      background-color: $btn-normal;
      transition: background-color .7s ease;
      color: #fff;
      border: none;
      cursor: pointer;

    &:hover {
      background-color: map-get($btn-color-theme, hovered);
    }
    }
  }
  .community {
    display: none;
    cursor: pointer;

    @include desktop {
      display: inline-block;
      position: relative;
    }
  }

  .community-accordion {
    padding: 20px 15px 25px 15px;
    background-color: map-get($brand-color-theme, secondary-color);
    font-weight: 300;
    position: fixed;
    clip: rect(auto, auto, 0, auto);
    top: 114px;
    li:not(:last-child) {
      margin-bottom: 4px;
      
    }
    .community-tel {
      @extend %dropdown-arrow;
    }
    .community-tel:hover + .community-dropdown {
      max-height: 200px;
      transition: max-height .7s ease;
    }
    .community-dropdown {
      max-height: 0;
      overflow: hidden;
      transition: max-height .7s ease;
    }
    
  }

  .shopping-interactivity {
    display: flex;
    align-items: center;

    @include desktop {
      margin-left: 25px;
    }

    @include desktop {
      margin-left: 50px;
    }

    .liked {
      display: none;
      width: 25px;
      height: 23px;
      margin-left: 42px;
      cursor: pointer;


      @include desktop {
        display: inline-block;

        .liked-icon {
          fill: transparent;
          stroke: white;
          stroke-width: 2px;
        }
      }
    }
  }


  .cart {
    display: flex;
    position: relative;
    align-items: center;
    color: $main-font-color;
    cursor: pointer;

    @include desktop {
      padding-left: 15px;

      .cart-icon svg {
        width: 27px;
        height: 27px;
      }
    }

    @include desktop {
      padding-left: 42px;
    }


    .counter {
      padding-left: 3px;
    }
  }

  .nav {
    transform: translateX(-110%);
    position: absolute;
    z-index: 30;
    width: 90%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: #fff;
    overflow-y: auto;
    overflow-x: hidden;
    transition: transform .7s ease;

  
  

    @include desktop {
      transform: none;
      position: static;
      width: 100%;
      height: auto;
      margin-bottom: 26px;
    }
  }

  .main-logo {
    max-width: 132px;
    fill: white;

    @include desktop {
      max-height: 29px;
    }
  }

  .nav-container {
    display: flex;
    flex-direction: column;

    @include desktop {
      flex-direction: row;
    }

    .dropdown-category {
      @include fontSize(20px, 500);
    }

    .nav-icons {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 20px;

      @include desktop {
        display: none;
      }

      .close-icon {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 36px;
        height: 36px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;

        &:after {
          content: '';
          display: inline-block;
          position: absolute;
          width: 14px;
          height: 2px;
          border-bottom: 2px solid #606C38;
          border-radius: 2px;
          transform: rotate(45deg);
        }

        span {
          display: inline-block;
          position: absolute;
          width: 14px;
          height: 2px;
          border-bottom: 2px solid #606C38;
          border-radius: 2px;
          transform: rotate(-45deg);
        }
      }
    }

    .category {
      border-top: 1px solid rgba(0, 0, 0, 0.1);
      position: relative;

      li {
        background-color: #deebc1;
      }

      a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        background-color: rgba(222, 235, 193, 0.5);
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        padding: 13px 33px 17px 20px;
        color: $list-color;
        cursor: pointer;
        @include fontSize ($font-body-large, 500);

        @include desktop {
          display: none;
        }
      }

      .category-item {
        overflow: hidden;

      }

      .category-item>a,
      .dropdown>a:first-child {
        @extend %nav-arrow;
      }
    }
  }

  .dropdown {
    padding: 0;
    overflow: hidden;
    width: 100%;
    top: 0;
    position: absolute;
    background-color: #fff;
    transform: translateX(-100%);
    transition: transform .7s ease;

    li {
      overflow: hidden;
    }


    .backward>a {
      flex-direction: row-reverse;
      justify-content: start;
      gap: 12px;
      background-color: #fff;
      @extend %nav-arrow;

      &::after {
        transform: scaleX(-1);
      }
    }
  }

  .dropdown-active {
    transform: translateX(0);
    transition: transform .7s ease;

    a {
      color: $list-color;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      @include fontSize ($font-body-large, 300);
    }
  }



  .support {
    padding: 20px 20px 5px 20px;

    

    @include desktop {
      width: 100%;
      padding: 8px 5px 11px 5px;
      background-color: map-get($brand-color-theme, secondary-color);
    }
    @include widescreen {
      padding: 8px 64px 11px 64px;
    }

    li,
    span {
      margin-bottom: 11px;
      @include fontSize($font-body-small, 300);

      @include desktop {
        margin: 0;
      }
    }

    span {
      display: block;
    }

    .account-icon {
      display: none;

      @include desktop {
        display: inline-block;
      }
    }

    &:nth-child(n) {
      cursor: pointer;
    }

    .tel,
    .customers,
    .lang,
    .currency {
      position: relative;
      &>:first-child {
        @extend %dropdown-arrow;
      }

    }

    .account {
      &>:last-child {
        @extend %dropdown-arrow;
      }
    }


  }
  .about {
    display: flex;
    justify-content: space-between;
    
    ul {
      padding: 0;
    }

  }

  .account {
    display: none;
  }

  .customers {
    display: none;
    @include desktop {
      display: block;
    }
  }

  
  .support-accordion {
    overflow: hidden;
    background-color: map-get($brand-color-theme, secondary-color);
    z-index: 50;
    position: absolute;
    text-align: center;
    padding: 0;
    max-height: 0;
    transition: max-height .5s ease;
    width: 100%;

  
    ul {
      padding: 6px 0 11px 0;
    }

  
  
    li {
      padding-left: 0;
    }
  
    & :not(:last-child) {
      margin-bottom: 8px;
    }

    @include desktop {
      position: fixed;
      width: auto;
      top: 39px;


      ul {
        padding: 8px 10px 16px;
      }
  
    }


  }


  





  @include desktop {
    .about {
      display: flex;
      align-items: center;
      justify-content: flex-end;


      >li+li {
        margin-left: 30px;
      }

      .tel {
        &::after {
          display: none;
        }
      }

      .account {
        display: flex;
      }
      .customers {
        padding: 0;
      }

      .lang {
        margin-left: 81px;

      } 

      .account-icon+a {
        padding-left: 9px;
      }

      .currency {
        margin-left: 20px;
      }

    }
  }
  .accordion-active {
    .support-accordion {
      max-height: 300px;
      transition: max-height .5s ease;  
    }
    .community-accordion  {
      clip: rect(auto, auto, 700px, auto);
      transition: clip .7s ease;
      display: block;
    }
  }



  .catalog {
    display: none;


    @include desktop {
      display: flex;
      padding: 11px 22px 13px 20px;
      margin-left: 31px;
      color: #fff;
      @include fontSize($font-body-large, 500);
      background-color: $btn-normal;
      transition: background-color .7s ease;
      margin-left: 62px;
      gap: 116px;
      position: relative;
      cursor: pointer;

      &:hover {
        background-color: map-get($btn-color-theme, hovered);
        transition: background-color .7s ease;
      }


      .category-container {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: white;
        clip: rect(auto, 700px, 0, auto);
        transition: clip .7s ease;
  
        &>:first-child {
          margin-top: 21px;
        }
      }
  
      .category-item {
        padding: 20px 20px;
        color: black;
        @include fontSize($font-body-large, 500);
        display: flex;
        justify-content: space-between;
  
        &:after {
          content: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7 7L1 13' stroke='%23606C38' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
          display: inline-block;
        }
      }
  
      .dropdown {
        position: absolute;
        left: 100%;
        top: 0;
        transform: translateX(0);
        max-height: 0;
        // TODO max-height: 0;
        transition: max-height .7s ease;
  
        li {
          white-space: nowrap;
          background-color: map-get($brand-color-theme, secondary-color);
          padding: 20px 20px;
          color: black;
          @include fontSize($font-body-large, 300);
  
          &:not(:last-child) {
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
          }
    
        }
      }
  
      .category-item {
        border-top: 1px solid rgba(0, 0, 0, 0.1);
        position: relative;
  
  
        .backward {
          display: none;
        }
  
        &:hover {
          .dropdown {
            max-height: 700px;
            transition: max-height .7s ease;
            z-index: 30;
          }
        }
  
      }
      
  
      .category-active {
        clip: rect(auto, 700px, 700px, auto);
        transition: clip .5s ease;
      }
  
    }
  }


  .hidden,
  .nav-container .category a.hidden {
    //display: none;
    height: 0;
    width: 0;
    padding: 0;
    //overflow: hidden; //TODO fixing dropdown
    position: absolute;
  }

  .hidden-opacity {
    visibility: hidden;
    opacity: 0;
  }



}
.header .active-transform, .active-transform {
  transform: translateX(0);
  transition: transform .7s ease;
}


.header-scroll {
  background-color: #606C38;
  transition: background-color .5s ease;
}

// icons
.catalog-icon {
  max-width: 17px;
  max-height: 17px;
  margin-left: 58px;
}

.cart-icon {
  width: 27px;
  height: 27px;
}
