$primary-btn-color: #606C38;

.catalog-header {
    background-color: map-get($brand-color-theme, primary-color);
}

.catalog-header {
    position: sticky;
}


.catalog-page {
    margin-top: 66px;
    @include desktop {
        margin-top: 0px;

    }

    .current-category {
        display: flex;
        align-items: center;
        margin-bottom: 17px;


        h1 {
            margin-left: 16px;
            @include fontSize(20px, 500);

            @include desktop {
                font-size: 32px;
                margin-left: 0;
            }
        }

        span {
            margin-left: 9px;
            color: rgba(0, 0, 0, 0.4);
            font-size: 12px;
            @include fontSize(12px, 300);

            @include desktop {
                margin-left: 15px;
                font-size: $font-body-large;
            }
        }
    }

    .catalog-category {
        display: flex;
        margin-left: 59px;
        margin-bottom: 45px;
    }

    .category-thumb {
        padding: 8px 18px;
        min-width: 140px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
    }

    .counter {
        margin-left: 8px;
    }

    .products {
        display: flex;
    }

    .accordion-item {
        margin-bottom: 42px;
        position: relative;
    }

    .accordion-panel {
        display: flex;
        justify-content: space-between;
        margin-bottom: 23px;
        @include fontSize(18px, 500)
    }


    .range-values {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 15px;

        span {
            margin-left: 9px;
        }
    }

    .min-value,
    .max-value {
        flex-basis: 80px;
        width: 100%;
        @include fontSize(14px, 300);
        padding: 10px 0 10px 11px;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }

    .max-value {
        margin-left: 9px;
    }

    .submit-btn {
        background-color: $primary-btn-color;
        color: #fff;
        padding: 13px 10px;
        border: none;
        margin-left: 11px;
    }

    .range-bar {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        input[type=range] {
            position: relative;
            width: 232px;
            appearance: none;

            &::-webkit-slider-thumb {
                position: relative;
                z-index: 1;
                top: -7px;
                height: 18px;
                width: 18px;
                background: map-get($brand-color-theme, primary-color);
                border-radius: 50%;
                appearance: none;
            }

            &::-webkit-slider-runnable-track {
                width: 100%;
                height: 2px;
                background-color: map-get($brand-color-theme, primary-color);
            }
        }

        #max-price {
            position: absolute;
        }
    }


    .products-filter {
        position: relative;
        display: flex;
        align-items: center;
        background-color: map-get($brand-color-theme, secondary-color);

        @include desktop {
            padding: 0 60px 0 23px;
        }

        &>:nth-child(n) {
            cursor: pointer;
            width: 50%;
            padding: 23px 0;
            @include desktop {
                width: auto;
            }
        }
        
        & > :last-child {
            border-left: 1px solid rgba(0, 0, 0, 0.1);
            @include desktop {
                border-left: none;
            }
        }
    }

    .filters-thumb {
        display: flex;
        align-items: center;
        justify-content: center;
        & >:last-child {
            margin-left: 5px;
        }
        @include desktop {
            display: none;
        }
    }
    .filter-params {
        display: none;
        align-items: center;
        @include desktop {
            display: flex;
        }
        svg {
            margin-left: 8px;
        }
    }
    
    .params-item {
        @include fontSize(12px, 300);
        display: flex;
        align-items: center;
        padding: 9px 16px 9px 12px;
        background-color: #fff;
    }

    .params-item+.params-item {
        margin-left: 10px;
    }


    .filter-reset {
        @include fontSize(12px, 500);
        text-decoration: underline;
        margin-left: 15px;
    }

    .filter-soting {
        display: flex;
        justify-content: center;
        @include desktop {
            justify-self: end;
            position: absolute;
            right: 60px;    
        }

        span {
            margin-left: 10px;
        }
    }

    .disabled {
        display: none;
    }

    .brand-category {
        display: flex;
        margin-bottom: 45px;
        margin-left: 14px;
        overflow-x: scroll;

        @include desktop {
            margin-left: 0;
            overflow-x: hidden;
        }


        .brand-item {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 140px;
            height: 40px;
            padding: 8px 6px;
            background-color: map-get($brand-color-theme, bg-white);
            transition: all .5s ease;

            &:hover {
                background-color: map-get($brand-color-theme, primary-color)
            }

            img {
                max-width: 78px;
            }
        }

        .brand-item+.brand-item {
            margin-left: 7px;
        }

        .category-count {
            margin-left: 8px;
        }

    }

    .products-row {
        width: 60%;
    }

    .aside-filter {
        position: absolute;
        width: 100%;
        background-color: map-get($brand-color-theme, bg-white);
        padding: 28px 25px;
        @include fontSize($font-body-large, 300);
        display: none;

        @include desktop {
            width: 22.5%;
            position: static;
            display: block;
        }
    }


    .accordion-item:hover .accordion-filtes {
        max-height: 100vh;
    }


    .accordion-filtes {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        max-height: 0;
        transition: all .7s ease;
    }

    .filter-item {
        display: flex;
        align-items: center;
        margin-bottom: 16px;

        label {
            margin-left: 12px;
        }

        input[type="checkbox"] {
            margin: 0;
            appearance: none;
            width: 16px;
            height: 16px;
            border: 1px solid map-get($brand-color-theme, bg-gray);
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s ease;

            &:checked {
                background-color: map-get($brand-color-theme, primary-color);

                &::after {
                    content: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4L3.1 7L8 1' stroke='white' stroke-width='2'/%3E%3C/svg%3E%0A");
                    display: inline-block;
                }
            }
        }

        .item-count {
            margin-left: 4px;
            color: rgba($color: #000, $alpha: 0.5);
        }

    }

    .products-row {
        
        .card-holder {
            flex-wrap: wrap;

            &>:nth-child(n + 3) {
                display: flex;
            }
        }

        .card {
            margin-left: -1px;
            width: calc(50% + 1px);
            margin-bottom: -1px;


            @include desktop {
                width: calc(20% + 1px);
            }
        }


    }

    .load-more {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        padding: 35px 0 16px 0;
        width: 100%;
        border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
        border-right: 1px solid rgba($color: #000000, $alpha: 0.1);
        cursor: pointer;

        @include desktop {
            background-color: map-get($brand-color-theme, bg-white);
            padding: 28px 5px;
        }

        .reload-icon {
            margin-right: 17px;
        }
    }
}

.pagination {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;

    @include desktop {
        border: 1px solid rgba($color: #000000, $alpha: 0.1);
        background: map-get($brand-color-theme, bg-white);
        padding: 0;

        &>:last-child {
            border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
        }
    }


    .pagination-next,
    .pagination-prev {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        background-color: #fff;
        border-radius: 50%;
        border: rgba(0, 0, 0, 0.1);
        cursor: pointer;

        @include desktop {
            width: 280px;
            height: auto;
            padding: 28px 5px;
            margin: 0;
            border: none;
        }
    }

    .pagination-prev {
        transform: scale(-1, 1);
        margin-right: 8px;

        @include desktop {
            margin: 0;
        }
    }

    .pagination-next {
        margin-left: 8px;

        @include desktop {
            margin: 0;
        }
    }

    .pagination-holder {
        display: flex;
        flex-grow: 1;
        justify-content: center;
        align-items: stretch;
        align-self: stretch;

        &>:last-child {
            border-right: 1px solid rgba(0, 0, 0, 0.1);
        }

        &>:nth-child(n + 5) {
            display: none;

            @include desktop {
                display: flex;
            }
        }

        .pagination-active {
            border: 1px solid map-get($brand-color-theme, primary-color);
        }
    }


    .pagination-item {
        display: flex;
        max-width: 45px;
        height: 45px;
        flex-grow: 1;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        background-color: map-get($brand-color-theme, bg-white);
        border: 1px solid rgba($color: #000000, $alpha: 0.1);

        @include desktop {
            border: none;
            max-width: unset;
            height: auto;
            border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
        }
    }

}

.pagination-item+.pagination-item {
    margin-left: 4px;

    @include desktop {
        margin: 0;
    }
}

.seo-link {
    display: flex;
    justify-content: space-between;
    overflow-x: scroll;

    .link-item {
        display: block;
        min-width: 150px;
        padding: 9px 0;
        background-color: map-get($brand-color-theme, bg-white);
        border: 1px solid rgba($color: #000000, $alpha: 0.1);
        text-align: center;
    }

    .link-item+.link-item {
        margin-left: 4px;

        @include desktop {
            margin: 0;
        }
    }
}

.faq-section {
    @include fontSize($font-body-small, 300);

    &>:nth-child(n) {
        margin-bottom: 85px;
    }

    .card-title {
        padding: 8px 14px;
        background: map-get($brand-color-theme, secondary-color);
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include desktop {
            padding: 25px 27px;
            padding-right: 76px;
        }


        h2 {
            @include fontSize(20px, 500);

            @include desktop {
                @include fontSize(32px, 500);
            }
        }
    }

    h2 {
        @include fontSize(20px, 500);

        @include desktop {
            @include fontSize(32px, 500);
        }
    }

    .buttons {
        display: flex;
        gap: 10px;

        &>:nth-child(n) {
            width: 38px;
            height: 38px;

            @include desktop {
                width: 45px;
                height: 45px;
            }

        }

        .row-prev-btn {
            transform: scale(-1, 1);
        }


        .disabled {
            path {
                stroke: rgba(96, 108, 56, 0.3);
            }
        }

        .swiper-button-disabled {
            path {
                stroke: map-get($slider-btn-color-theme, disabled-inner);
            }

            circle {
                fill: map-get($slider-btn-color-theme, disabled-outer);
            }

            &:hover {
                circle {
                    fill: map-get($slider-btn-color-theme, disabled-outer);
                    transition: fill .7s ease;

                }

                path {
                    stroke: map-get($slider-btn-color-theme, disabled-inner);
                    transition: stroke .7s ease;
                }
            }

        }
    }

    .row-prev-btn,
    .row-next-btn {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        transition: all .5s ease;

        &:hover {
            circle {
                fill: map-get($slider-btn-color-theme, hovered-outer);
                transition: fill .7s ease;

            }

            path {
                stroke: map-get($slider-btn-color-theme, hovered-inner);
                transition: stroke .7s ease;
            }
        }
    }

    .review-holder {
        display: flex;
        overflow-x: scroll;

        @include desktop {
            overflow-x: hidden;
        }

        .card {
            width: 224px;
            background-color: map-get($brand-color-theme, bg-white);
            padding: 26px 20px;
            box-sizing: border-box;

            @include desktop {
                width: 256px;
            }
        }

    }

    .card+.card {
        border-left: 1px solid rgba(0, 0, 0, 0.1);
    }

    .review-title {
        display: flex;
        flex-direction: column;
        padding-bottom: 15px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);

        .reviewer-name {
            @include fontSize(18px, 500);
            margin-bottom: 4px;
        }

        .rewiew-date {
            color: rgba($color: #000000, $alpha: 0.4);
        }
    }

    .review-product {
        display: flex;
        align-items: center;
        padding: 12px 0;
        margin-bottom: 16px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);

        .product-title {
            color: map-get($brand-color-theme, primary-color);
            margin-left: 11px;
            max-width: 150px;
        }
    }



}

.questions-answers {
    @include fontSize($font-body-large, 300);

    .faq-title {
        margin-bottom: 19px;
        margin-left: 16px;

        @include desktop {
            margin-left: 0;
        }
    }

    .faq-item {
        margin-bottom: 10px;
    }

    .item-title {
        display: block;
        padding: 14px 40px;
        background-color: map-get($brand-color-theme, bg-white);
        @extend %catalog-arrow;
        display: flex;
        justify-content: space-between;
    }

    .accordion-content {
        max-height: 0;
        overflow: hidden;
        transition: all .7s ease;
    }

    .accordion-holder {
        padding: 20px 40px 20px 25px;

        span {
            display: block;
            margin-bottom: 4px;
        }

        li span {
            display: flex;
            align-items: center;

            &::before {
                content: '';
                display: block;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #000;
                margin-right: 7px;
            }
        }
    }
    .accordion-active {
        .item-title::after {
            transform: rotate(180deg);
        }
        .accordion-content {
            max-height: 500px;
            transition: all .7s ease;
        }
    }
}

.price-list {
    .price-title {
        margin-bottom: 13px;
        margin-left: 16px;

        @include desktop {
            margin-left: 0;
        }
    }

    table {
        border: unset;
        width: 100%;

        thead {
            background-color: map-get($brand-color-theme, primary-color);
            color: map-get($brand-color-theme, bg-white);

            th {
                @include fontSize($font-body-large, 300);
                padding: 14px 15px;
                text-align: left;

                @include desktop {
                    padding: 16px 43px;
                }
            }
        }

        tbody {
            tr {
                background-color: map-get($brand-color-theme, bg-white);

                &:nth-child(2n + 2) {
                    background-color: rgba($color: map-get($brand-color-theme, secondary-color), $alpha: 0.4);
                }
            }

        }

        td {
            @include fontSize($font-body-large, 300);
            padding: 16px 43px;
            text-align: left;
        }

        tr>:last-child {
            white-space: nowrap;
            padding: 21px 40px;
            text-align: center;
        }
    }

    tbody tr> :last-child {
        font-weight: 500;
    }

    table,
    tr,
    td,
    th {
        border: 1px solid rgba($color: #000000, $alpha: 0.1);

        @include desktop {
            border: 1px solid map-get($brand-color-theme, bg-white);
        }
    }

    table {
        border-left: none;
        border-right: none;
    }


}

.viewed-products {
    overflow: hidden;
    .card-holder {
        flex-wrap: nowrap;

        .card {
            width: 50vw;

            @include desktop {
                width: 20%;
            }
        }

        &>:nth-child(n + 3) {
            display: none;

            @include desktop {
                display: flex;
            }
        }
    }

    .liked {
        stroke: black;
        stroke-width: 2px;
        fill: white;
        transition: fill .5s ease;

        &:hover {
            fill: map-get($brand-color-theme, primary-color);
            stroke: map-get($brand-color-theme, primary-color);
        }
    }
}



