//temp fixes
$base-font: 14px;
$large-font: 16px;


.body-info {
    .header {
        background: #606C38;
        position: sticky;
    }
}

.container_flex {
    flex-direction: column;

    @include desktop {
        display: flex;
        flex-direction: row;
        margin-bottom: 110px;
    }
}
.navigation-aside {
    width: 100%;
    margin-bottom: 11px;
    background: #fff;
    flex-shrink: 0;

    @include desktop {
        width: 300px;
        border-right: 1px solid rgba(0, 0, 0, 0.1);
        margin-bottom: 0;
    }
}
.navigation-aside__title {
    font-size: $large-font;
    font-weight: 500;
    padding: 10px 20px 16px 20px;
    @extend %catalog-arrow;
    justify-content: space-between;
    @include desktop {
        &::after {
            display: none;
        }
        font-size: $large-font * 1.625;
        padding: 18px 0 22px 40px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
}

.navigation-aside__link {
    padding-bottom: 25px;
    padding-left: 27px;
    font-size: $large-font;
    font-weight: 300;
    text-decoration: underline;
    display: block;
    border-left: 1px solid transparent;

    transition: all .5s ease;
    @include desktop {
        padding: 13px 0 15px 40px;
        font-size: $large-font * 1.125;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
    &:hover {
        color: #606C38;
        border-left: 1px solid #606C38;
        background: rgba(222, 235, 193, 0.3);
    }
}
.navigation-aside__item:first-of-type {
    padding-top: 30px;
    @include desktop {
        padding-top: 0;
    }
}

.content {
    padding: 18px 16px 73px 16px;
    margin-bottom: 65px;
    background: #fff;
    @include desktop {
        margin-bottom: 0;
        padding: 40px 68px 121px 68px;
    }
}

.content__title {
    text-align: center;
    font-size: $large-font * 1.25;
    margin-bottom: 27px;
    @include desktop {
        font-size: $large-font * 2;
        text-align: left;
        margin-bottom: 46px;
    }
}
.navigation-aside__content {
    max-height: 0px;
    overflow: hidden;
    transition: all .5s ease;
    @include desktop {
        max-height: initial;
    }
}
.navigation-aside__content_active {
    max-height: 700px;
}

.content__body {
    h1, h2, h3, h4, h5, h6 {
        font-weight: 500;
        margin-bottom: 10px;
        @include desktop {
            margin-bottom: 15px;
        }
    }

    h2 {
        font-size: $large-font;
        @include desktop {
            font-size: $large-font * 1.625;
        }
    }

    h3 {
        font-size: $large-font;
        @include desktop {
            font-size: $large-font * 1.125;
        }
    }
    h4 {
        font-size: $large-font;
        @include desktop {
            font-size: $large-font * 1.125;
        }
    }
    p {
        font-size: $base-font;
        font-weight: 300;
        margin-bottom: 22px;
        @include desktop {
            font-size: $large-font;
        }
    }
    ul {
        margin-bottom: 8px;
        font-weight: 300;
        @include desktop {
            margin-bottom: 14px;
        }
        li {
            line-height: 21px;
            display: flex;
            align-items: center;
            &::before {
                content: '';
                display: block;
                width: 20px;
                height: 1px;
                background: #606C38;
                flex-shrink: 0;
                margin-right: 11px;
            }
        }
    }
}
