.main-product {
    .nav-row {
        display: flex;
        justify-content: space-between;
        font-size: $large-font * 1.125;
        font-weight: 500;
        background: #DEEBC1;
        color: rgba(96, 108, 56, 0.6);
    }
    .nav-row__item {
        padding-top: 16px;
        padding-bottom: 21px;
        width: 20%;
        text-align: center;
        position: relative;
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            width: 1px;
            height: 20px;
            background: rgba(96, 108, 56, 0.3);
        }
    }
    .nav-row__item:last-of-type {
        &::after {
            display: none;
        }
    }
    .product-holder__content {
        display: flex;
    }

    .product-holder__swiper, .product-holder__descr {
        width: 50%;
    }
    .product-holder__descr {
        display: flex;
        flex-direction: column;
    }
    //descr stylesheet
    .product-holder__descr-title {
        font-size: $large-font * 2;
        font-weight: 500;
        margin-bottom: 45px;
        margin-top: 45px;
    }
    .product-holder__shipping {
        display: flex;
        margin-bottom: 30px;
    }
    .product-holder__shipping-item {
        padding: 8px 14px;
        border: 1px solid rgba($color: #000000, $alpha: 0.1);
    }
    .product-holder__shipping-item:not(:first-of-type) {
        margin-left: 12px;
    }
    .product-holder__rate {
        margin-bottom: 43px;
    }
    .product-holder__summary {
        display: flex;
    }
}