.cart-popup {
    position: absolute;
    width: 100%;
    height: 0;
    padding: 0;
    z-index: 20;
    //transition styles
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: opacity .7s ease;

    .cart-wrapper {
        @include desktop {
            padding: 60px 120px 120px;
            //margin: 60px 120px 120px;
        }
    }

    
    .cart-container {
        padding: 18px 16px;
        background-color: #EFEFEF;
        position: relative;
        z-index: 15;
        
        @include desktop {
            padding: 50px 80px;
            height: auto;
        }
    }


    .cart-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        padding: 15px;
        background: #fff;
        z-index: 15;
        
        
        span {
            @include fontSize(20px, 500);
        }
        
        @include desktop {
            padding: 18px 18px 18px 81px;
            span {
                @include fontSize(26px, 500);
            }
            
        }

    }
    .close-icon {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 36px;
        height: 36px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        transition: all .7s ease;

        &:hover {
            background-color: $btn-brand-hovered;
            

            span {
                border-bottom: 2px solid #fff;
            }

            &::after {
                border-bottom: 2px solid #fff;
            }
        }

        &:after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(45deg);
        }

        span {
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 2px;
            border-bottom: 2px solid #606C38;
            border-radius: 2px;
            transform: rotate(-45deg);
        }

        @include desktop {
            width: 45px;
            height: 45px;                
        }
    }


    .cart-count {
        @include fontSize(16px,300);
    }

    .shop-title {
        display: none;
        
        @include desktop {
            padding: 15px 45px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: #DEEBC1;            
        }
    }
    .shop-decription {
        display: flex;
        align-items: center;
        span {
            @include fontSize(16px, 300);
        }
    }

    .shop-img {
        margin-left: 13px;
    }

    .cart-holder {
        display: flex;
        flex-direction: column;
        background: #fff;
        position: relative;
        overflow: scroll;

        @include desktop {
            overflow: auto;
        }
    }

    .cart-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 22px 20px 24px 20px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        position: relative;
        //
        flex-wrap: wrap;
        
        span {
            @include fontSize(14px, 300);
            
        }
        @include desktop {
            padding: 25px 35px;
            
            span{
                @include fontSize(16px, 300);
            }
        }
    }
    

    .item-holder {
        display: flex;
        align-items: center;
    }

    .item-col {
        display: flex;
        justify-content: center;
        align-items: center;
        //
        margin-bottom: 20px;

        @include desktop {
            margin-bottom: 0;
        }

        img {
            object-fit: contain;
            width: 75px;
            height: 75px;
            
            @include desktop {
                width: 95px;
                height: 95px;                
            }
        }
    }

    .item-descr {
        display: flex;
        margin-left: 21px;
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: wrap;


        @include desktop {
            flex-direction: row;
            align-items: center;
            margin-left: 50px;
        }
    }


    .item-title {
        margin-bottom: 15px;
        max-width: 166px;
        @include desktop {
            max-width: 260px;
            margin: 0;
        }
    }

    .item-size {
        @include desktop {
            margin-left: 73px;
        }
    }


    .item-counter {
        display: flex;
        align-items: center;

        .minus-btn {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 10px;
            height: 10px;

            span {
                display: block;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
            }
        }

        .plus-btn {
            margin-left: 12px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            width: 10px;
            height: 10px;

            &::after {
                content: '';
                display: block;
                position: absolute;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
                transform: rotate(90deg);
            }

            span {
                display: block;
                position: absolute;
                width: 100%;
                height: 2px;
                background: rgba(0, 0, 0, 0.2);
            }
        }

        .item-count {
            margin-left: 12px;
            width: 50px;
            @include fontSize(14px, 300);
            border: 1px solid rgba(0, 0, 0, 0.1);
            color: #000;
            text-align: center;
            padding: 5px 0;

        }
    }
    .cart-price {
        align-self: center;
        span {
            @include fontSize(18px,500);
        }
    }

    .price-discount {
        display: none;
    }

    .cart-total {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        padding: 25px 20px 21px 20px;
        background-color: #EFF0EB;
        border: 1px solid rgba(96, 108, 56, 0.3);
        position: sticky;
        width: 100%;
        bottom: 68px;
        
        @include desktop {
            padding: 30px 30px 44px 30px;
            position: static;
            margin-bottom: 30px;
            background-color: #fff;
            border: unset;
        }


        button {
            @include fontSize(16px, 500);
            padding: 15px 10px;
            background-color: #606C38;
            border: none;
            color: #fff;
            align-self: stretch;
            cursor: pointer;
            
            @include desktop {
                padding: 15px 117px;
                align-self: flex-end;

            }
        }
    }

    .sum-container {
        display: flex;
        align-items: center;
        margin-bottom: 11px;
        justify-content: space-between;
        width: 100%;

        @include  desktop {
            margin-bottom: 14px;
            justify-content: flex-end;
            width: auto;
            span {
                @include fontSize(18px, 500);
            }
        }

        span {
            @include fontSize(18px, 300);
        }

        .total-sum {
            margin-left: 22px;
            @include fontSize(18px, 500);

            @include desktop {
                @include fontSize(26px, 500);
            }
        }
    }
    .cart-etc {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        //
        position: absolute;
        top: 19%;
        right: 19px;
        cursor: pointer;

        @include desktop {
            position: static;
        }

        span,
        &::after,
        &::before {
            width: 3px;
            height: 3px;
            border-radius: 100%;
            background-color: #000;
            overflow: hidden;
        }

        &::before,
        &::after {
            content: '';
            display: block;
        }

        span,
        &::before {
            margin-bottom: 3px;
        }
    }
    .cart-overlay {
        position: fixed;
        width: 100%;
        height: 100%;
    }
    }
