.cart-active {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    display: block;
    //transition styles
    visibility: visible;
    opacity: 1;
    background: rgba(0, 0, 0, .4);
    transition: opacity .5s ease;

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* for Internet Explorer, Edge */
    scrollbar-width: none;
    /* for Firefox */
    overflow-y: scroll;

}
