#footer {
  background: #1C2D10;
  margin-bottom: 55px;

  @include desktop {
    margin-bottom: 0;
  }

  .container {
    max-width: 100%;
  }

  .subscription {
    background: map-get($brand-color-theme, primary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    padding: 45px 15px 50px 15px;

    @include desktop {
      flex-direction: row;
      gap: 42px;
      padding: 61px 0 67px 0;
    }

    h1 {
      @include fontSize(18px, 500);
      color: #fff;
      max-width: 440px;

      @include desktop {
        @include fontSize(26px, 500);
      }
    }

    .subscription-holder {
      display: flex;
      align-items: stretch;
    }

    #subscription-input {
      @include fontSize(18px, 300);
      padding: 9px 0 14px 12px;
      background: #fff;
      width: 263px;
      border: none;

      @include desktop {
        width: 494px;
        padding: 13px 0 20px 18px;
      }

      &:focus {
        outline: none;
      }
    }

    .subscription-btn {
      padding: 16px 29px;
      color: #fff;
      background: rgba(255, 255, 255, 0.2);
      border: none;
      cursor: pointer;
      @include colorTransition($btn-subscription-hovered);


      @include desktop {
        padding: 13px 25px 18px 25px;
      }

      span {
        display: none;
        @include fontSize(18px, 500);

        @include desktop {
          display: inline-block;
        }
      }
    }
  }

  .submit-mobile {
    @include desktop {
      display: none;
    }
  }

  .footer-content {
    display: flex;
    justify-content: space-between;
    color: #fff;
    flex-direction: column;
    padding: 0 15px 0 15px;

    @include desktop {
      flex-direction: row;
      padding: 0;
    }

    span {
      @include fontSize(16px, 300);
    }

    li {
      color: rgba(255, 255, 255, 0.6);
      @include fontSize(18px, 300);
      margin-bottom: 17px;

      a {
        transition: color .5s ease;

        &:hover {
          color: #fff;
          transition: color .5s ease;
        }
      }
    }

    h2 {
      @include fontSize(18px, 500);
      margin-bottom: 10px;

      @include desktop {
        margin-bottom: 15px;

      }
    }
  }

  .footer-top,
  .footer-socials {
    flex-basis: 20%;
  }

  .accordion-item,
  .footer-socials {
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    @include desktop {
      border-left: 1px solid rgba(255, 255, 255, 0.1);

    }
  }

  .footer-top,
  .accordion-item,
  .footer-socials {
    //padding-left: 16px;
    padding-top: 19px;
    padding-bottom: 15px;

    @include desktop {
      padding-left: 55px;
      padding-bottom: 76px;
      padding-top: 49px;
    }
  }


  .accordion {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;

    @include desktop {
      flex-direction: row;
    }

    h2 {
      display: flex;
      justify-content: space-between;
      cursor: pointer;

      &::after {
        display: inline-block;
        content: url("data:image/svg+xml,%3Csvg width='15' height='10' viewBox='0 0 15 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 8.5L7.5 1.5L14 8.5' stroke='white' stroke-width='2'/%3E%3C/svg%3E%0A");
        transform: scale(1, -1);
        transition: transform .5s ease;
      }

      @include desktop {
        &::after {
          display: none;
        }
      }
    }

    .accordion-active {
      &::after {
        transform: scale(1, 1);
        transition: transform .5s ease;
      }

      .accordion-content {
        max-height: 1000px;
        transition: all .7s ease;
      }
    }
  }


  .accordion-item {
    flex-basis: 33.333%;
    padding-top: 15px;
    position: relative;
    transition: all .7s ease;

    @include desktop {
      padding-top: 49px;
    }
  }

  .accordion-content {
    overflow: hidden;
    position: relative;
    display: block;
    transition: all .7s ease;
    max-height: 0;


    @include desktop {
      max-height: 2000px;
    }
  }

  .accordion-open {
    &::after {
      transform: scale(1, 1);
    }
  }


  .footer-logo {
    margin-bottom: 21px;

    @include desktop {
      margin-bottom: 29px;
    }
  }

  .footer-tel {
    margin-bottom: 12px;

    a {
      @include fontSize (20px, 500);
    }

    span {
      @include fontSize(18px, 500);
      display: block;
      margin-bottom: 13px;
    }
  }

  .schedule {
    margin-bottom: 8px;

    &+span {
      color: rgba(255, 255, 255, 0.6);
    }
  }

  .marketplace {
    margin-bottom: 19px;
    display: flex;
    gap: 13px;

    @include desktop {
      display: block;
      margin-bottom: 51px;
    }

    img {
      max-width: 127px;
    }

    &>:first-child {
      @include desktop {
        margin-bottom: 10px;
      }
    }

  }

  .socials-container {
    display: flex;
    gap: 12px;
  }

  .footer-bottom {
    font-family: 'Oswald'Arial, Helvetica, sans-serif;
    color: rgba(255, 255, 255, 0.6);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px 43px 15px;
    @include fontSize (14px, 300);

    @include desktop {
      padding: 25px 60px 25px 60px;
      border-top: 1px solid rgba(255, 255, 255, 0.1);

    }
  }

  .double-col {
    order: -1;
    margin-bottom: 17px;

    @include desktop {
      margin-bottom: 0;
    }

    a+a {
      margin-left: 75px;
    }
  }

  .company {
    display: none;

    @include desktop {
      display: inline;
    }
  }
}