.breadcrumbs {
    display: flex;
    font-family: $font-family;
    @include fontSize(14px, 300);
    margin-bottom: 32px;
    margin-left: 14px;
    @include desktop {
        margin-left: 0;
    }

    li+li {
        margin-left: 5px;

        &::before {
            content: '/';
        }

        a {
            margin-left: 5px;
        }
    }

    .active {
        color: #606C38;
    }
}